import React, { useRef, useEffect } from "react";
import styled from "styled-components";

const AdPart: React.FC = () => {
  // Ïπ¥Ïπ¥Ïò§ ÏóêÎìúÌïè Í¥ëÍ≥†
  // useRefÌõÖÏù¥ div HTML ÏöîÏÜåÎ•º Ï∞∏Ï°∞ÌïòÍ∏∞ ÎïåÎ¨∏Ïóê HTMLDivElementÎ•º ÌÉÄÏûÖÏúºÎ°ú ÏßÄÏ†ï
  const scriptElement = useRef<HTMLDivElement | null>(null);

  // script ÌÉúÍ∑∏Î•º ÎèôÏ†ÅÏúºÎ°ú Ï∂îÍ∞Ä
  useEffect(() => {
    const script = document.createElement("script");
    script.setAttribute("src", "https://t1.daumcdn.net/kas/static/ba.min.js");
    script.setAttribute("charset", "utf-8");
    script.setAttribute("async", "true");

    // refÏùò ÌòÑÏû¨ Í∞í Î≥ÄÏàòÏóê Ï†ÄÏû•
    const currentScriptElement = scriptElement.current;

    if (currentScriptElement) {
      currentScriptElement.appendChild(script);
    }

    // cleanup Ìï®Ïàò -  Î∂àÌïÑÏöîÌïú scriptÌÉúÍ∑∏
    return () => {
      if (currentScriptElement) {
        currentScriptElement.removeChild(script);
      }
    };
  }, []);

  return (
    <AdContainer>
      <div ref={scriptElement}>
        <ins
          className="kakao_ad_area"
          style={{ display: "none" }}
          data-ad-unit="DAN-vGny0Q5scRDlw6pg"
          data-ad-width="728"
          data-ad-height="90"
        />
      </div>
    </AdContainer>
  );
};

export default AdPart;

const AdContainer = styled.div`
  display: flex;
  align-items: center;
  background-color: #272727;
  justify-content: center;
  width: 100%;
  height: fit-content;
  padding: 20px 0;
  margin: auto;
  margin-bottom: 48px;
`;
