import axios from "axios";
import { MemberRankList } from "model/userType";

// Ïû¨ÌïôÏÉù rank GET ÏöîÏ≤≠
const getJuniorRank = async (memberGen: number) => {
  try {
    const config = {
      withCredentials: true,
      params: {
        memberGen,
      },
    };

    const response = await axios.get<MemberRankList[]>(
      `/api/rank/junior`,
      config
    );

    if (response.status !== 204) {
      return response.data;
    }
    return []; // Îç∞Ïù¥ÌÑ∞Í∞Ä ÏóÜÏùÑ Îïå Îπà Î∞∞Ïó¥ Î∞òÌôò
  } catch (error: unknown) {
    console.log(error);
    return []; // Ïò§Î•ò Î∞úÏÉù Ïãú Îπà Î∞∞Ïó¥ Î∞òÌôò
  }
};
export default getJuniorRank;
