import axios from "axios";
import { Problem } from "model/problemType";
import { Location } from "react-router-dom";

// Î¨∏Ï†ú Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ (get)
// TODO: Í¥ÄÎ¶¨Ïûê Î¨∏Ï†úÎ∂àÎü¨Ïò§Í∏∞ ÌÉÄÏûÖÏùÄ SoluProbÎ°ú
const getList = async (
  setList: React.Dispatch<React.SetStateAction<Problem[]>>,
  location: Location<any>
): Promise<void> => {
  // Ï°∞Í±¥Ïóê Îî∞Îùº uriÎ•º ÏÑ§Ï†ï
  const uri =
    location.pathname === "/manageFeed"
      ? `/api/admin/problemlist`
      : `/api/problemlist`;

  try {
    const config = {
      withCredentials: true,
    };

    const response = await axios.get<Problem[]>(uri, config);
    setList(response.data);
  } catch (error: unknown) {
    console.log("Failed to fetch list:", error);
  }
};
export default getList;
